[data-notify="progressbar"] {
	margin-bottom: 0px;
	position: absolute;
	bottom: 0px;
	left: 0px;
	width: 100%;
	height: 5px;
}
.ui-select span{
	display: none;
}
#nav-bg-shadow{
	z-index: 1000;
	background: $black;
	-webkit-transition: background 1s ease-in-out;
	-moz-transition: background 1s ease-in-out;
	-ms-transition: background 1s ease-in-out;
	-o-transition: background 1s ease-in-out;
	transition: background 1s ease-in-out;
	i{
		color: $white;
		-webkit-transition: background 1s ease-in-out;
		-moz-transition: background 1s ease-in-out;
		-ms-transition: background 1s ease-in-out;
		-o-transition: background 1s ease-in-out;
		transition: background 1s ease-in-out;
	}
}
.navbar{
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	padding-top: 0 !important;
	padding-bottom: 0 !important;
	#navbar-brand-carcare{
		padding: 0;
		position: absolute;
    top: 0;
		transition: all .5s ease-in-out;
		img{
			height: 1.5rem;
			transition: all .5s ease-in-out;
		}
	}
	#navbar-brand-logocarcare{
		padding: 0;
		position: absolute;
    top: 1vh;
		transition: all .5s ease-in-out;
		img{
			height: 3rem;
			transition: all .5s ease-in-out;
		}
		*{
			transition: all .5s ease-in-out;
		}
	}
	.form-group{
		margin: 0;
	}
	.nav-link{
		padding-top: .25rem !important;
		padding-bottom: 0 !important;
		transition: all .5s ease-in-out;
	}
	&-profil{
		position: absolute;
		width: 100%;
		ul li:not(:first-of-type) a{
			padding-top: 0.1rem !important;
		}
		.drawer-hamburger {
			position: initial;
			top: initial;
			display: initial;
			box-sizing: initial;
			width: 1rem;
			padding: 0;
			&-icon, &-icon:after, &-icon:before{
				background-color: $gray;
			}
			&-icon{
				&:before{
					top: -0.35rem;
				}
				&:after{
					top: 0.4rem;
				}
			}
		}
	}
}
.login{
	padding: 0;
	.btn-primary {
    color: #fff !important;
    background-color: #2196f3 !important;
    border-color: #2196f3 !important;
	}
	.modal-dialog{
	  margin: 0;
	  padding: 0;
		.modal-content{
			height: 100vh;
			width: 100vw;
			.modal-body{
				> div:first-of-type {
					border-right: 1px solid $thirdgray;
				}
				.col{
					> .container{
						padding-right: 5rem;
    					padding-left: 5rem;
					}
				}
				.card{
					background: $thirdgray;
					.card-body{
						i{
							float: right;
							font-size: 3rem;
						}
						.has-tooltip{
							position: relative;
							.tooltip{
								width: max-content;
								top: -50%;
								left: initial;
							}
						}
					}
				}
			}
		}
	}
}
.search{
	padding: 0;
	.modal-dialog{
	  margin: 0;
	  padding: 0;
		.modal-content{
			height: 100vh;
			width: 100vw;
			.modal-body{
				input {
				  border: 0;
				  background: rgba(0, 0, 0, 0);
				  font-size: 3rem;
				  letter-spacing: 0.2rem;
				  text-transform: uppercase;
				  font-weight: 600;
				  color: $gray;
				  padding-bottom: 1rem;
				}
				textarea, input { outline: none; }
				::-webkit-input-placeholder { /* Chrome/Opera/Safari */
				  font-size: 3rem;
					color: $gray;
				}
				::-moz-placeholder { /* Firefox 19+ */
					font-size: 3rem;
					color: $gray;
				}
				:-ms-input-placeholder { /* IE 10+ */
					font-size: 3rem;
					color: $gray;
				}
				:-moz-placeholder { /* Firefox 18- */
					font-size: 3rem;
					color: $gray;
				}
			}
		}
	}
}
.tvgps, .motoristps{
	.badge-light {
		color: #111 !important;
		background-color: #f8f9fa;
	}
	.badge-pill {
		padding-right: .6em;
		padding-left: .6em;
		border-radius: 10rem;
	}
	.badge {
		display: inline-block;
		padding: .25em .4em;
		font-size: 75%;
		font-weight: 700;
		line-height: 1;
		color: #fff;
		text-align: center;
		white-space: nowrap;
		vertical-align: baseline;
		border-radius: .25rem;
	}
	padding: 0;
	.modal-dialog{
	  margin: 0;
	  padding: 0;
		.modal-content{
			height: 100vh;
			width: 100vw;
			background: $darkgray;
			.modal-header{
				height: 0;
			}
			.modal-body{
				.card{
					&:not(first-of-type){
						overflow: auto;
					}
					&::-webkit-scrollbar {
						display: none;
					}
					width: 100%;
					height: 100%;
					margin: 0 auto;
					perspective: 600;
					position: relative;
					background: $darkgray;
				  	.card-body{
						background-color: $darkgray;
						position: absolute;
						padding: 0;
						width: 100%;
						height: 100%;
						transform-style: preserve-3d;
						transition: all .5s ease-in-out;
						& *{
							color: $white;
						}
						&:hover{
							transform: rotateY(180deg);
						}
						.card-face{
							backface-visibility: hidden;
							height: 100%;
							width: 100%;
							position: absolute;
							&:not(.active){
								transform: rotateY(180deg);
							}
							h1{
								font-weight: bolder;
								font-size: 5rem;
							}
						}
				  	}
				}
			}
		}
	}
}
.contactus{
	.modal-dialog{
		.modal-content{
			i{
				float: right;
				font-size: 3rem;
			}
			h5{
				margin-left: 1rem;
			}
			.modal-body{
				button{
					float: right;
				}
			}
		}
	}
}
.contactusmodallauncher {
	position: fixed;
	z-index: 995;
	top: 85vh;
	left: 2vw;
	i{
		font-size: 2.5rem;
	}
}
.firstsection{
	height: 100vh;
	padding: 2rem;
	&:before{
		content: "";
		position: absolute;
		left: 0;
		top: 0;
		height: 100%;
		width: 100%;
		background-image: url('../media/810.jpg');
		-webkit-background-size: contain;
		background-size: contain;
		opacity: .02;
	}
	.register-firstsection, .success-firstsection, .profil-firstsection, .reservation-firstsection{
		text-decoration: none;
		.linetop{
			position: absolute;
			top: 7vh;
			left: 0;
			height: 48vh;
			border-left: .4rem solid $thirdgray;
		}
		.linebottom{
			position: absolute;
			top: 72vh;
			left: 0;
			height: 18vh;
			border-left: .4rem solid $thirdgray;
		}
		ul.nav-social{
			position: absolute;
			left: 0;
			top: 63%;
			z-index: 999;
			.nav-link{
				padding: 0;
				font-size: 1.25rem;
			}
		}
	}
	.leftfirstsection{
		&:link {
			color: inherit;
		}
		&:visited {
			color: inherit;
		}
		&:hover {
			color: inherit;
		}
		&:active {
			color: inherit;
		}
		text-decoration: none;
		cursor: default;
		.carbg{
			cursor: pointer;
			background-image: url('../media/motorist.png');
			background-size: contain;
			background-repeat: no-repeat;
			background-position: center;
			position: absolute;
			width: 40vw;
			height: 40vw;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			&::after{
				font-family: 'Montserrat', sans-serif;
				content: 'MOTORIST';
				position: absolute;
				top: 50%;
				left: 50%;
				transform: rotate(180deg) rotateY(180deg) translate(-50%, 50%);
				z-index: 1;
				font-size: 5em;
				font-weight: bold;
			}
			
		}
		.linetop{
			position: absolute;
			top: 7vh;
			left: 0;
			height: 48vh;
			border-left: .4rem solid $thirdgray;
		}
		.linebottom{
			position: absolute;
			top: 72vh;
			left: 0;
			height: 18vh;
			border-left: .4rem solid $thirdgray;
		}
		ul{
			position: absolute;
			left: 0;
			top: 63%;
			.nav-link{
				padding: 0;
				font-size: 1.25rem;
			}
		}
		.contactinfo{
			font-size: 0.7rem;
			margin: 0;
			position: absolute;
			right: 0;
			bottom: 0;
		}
	}
	.col:not(first-of-type){
		text-decoration: none;
	}
	.rightfirstsection{
		a:link &{
			color: inherit;
		}
		a:visited & {
			color: inherit;
		}
		a:hover & {
			color: inherit;
		}
		a:active & {
			color: inherit;
		}
		&::after{
			font-family: 'Montserrat', sans-serif;
			content: 'TVG';
			transform: rotate(180deg) rotateY(180deg);
			z-index: 1;
			font-size: 5em;
			font-weight: bold;
		}
		height: 100%;
		padding: 0;
		-moz-border-radius: 0;
		-webkit-border-radius: 0;
		border-radius: 0;
		background-image: url('../media/black-and-white-car-vehicle-vintage.jpg');
		background-size: cover;
		.statisticsnumbers{
			position: absolute;
			bottom: 0;
			right: 1.2vw;
			li{
				text-align: right;
			}
			button{
				cursor: text;
				font-size: 2rem;
				color: $white !important;
				text-decoration: none;
			}
			button:hover{
				text-decoration: none;
			}
		}
	}
}
.secondsection{
	height: 80vh;
	margin: 0;
	.leftsecondsection{
		padding: 0;
		.carousel{
			vertical-align: middle;
			padding: 3rem;
			padding-top: 1rem;
			.card{
				height: 75vh;
				img{
					width: 50%;
			    display: block;
			    margin: 0 auto;
					margin-bottom: 3rem;
				}
				.table {
				  border-collapse: collapse;
				  border-style: hidden;
				}
				.lastdivider{
					margin-top: 4.5rem;
				}
				.socialmediatvg a{
					font-size: 1.5rem;
    			padding-top: 0;
				}
			}
			.carousel-control-next span, .carousel-control-prev span{
				color: $halfgray;
				font-size: 3rem;
			}
		}
	}
	.rightsecondsection{
		@include gradient200;
		img{
			height: 100%;
			float: right;
		}
		p{
			font-size: .8rem;
		}
		h3{
			position: absolute;
	    font-size: 20rem;
	    opacity: .25;
	    border: 1px solid;
		}
	}
}
.thirdsection{
	height: 80vh;
	margin: 0;
	.leftthirdsection{
		@include gradient20;
		img{
			height: 100%;
			float: left;
		}
		p{
			font-size: .8rem;
		}
		h3{
			position: absolute;
	    font-size: 8rem;
	    opacity: .25;
	    border: 1px solid;
		}
	}
	.rightthirdsection{
		padding: 0;
		.carousel{
			vertical-align: middle;
			padding: 3rem;
			padding-top: 1rem;
			.card{
				height: 75vh;
				img{
					width: 50%;
			    display: block;
			    margin: 0 auto;
					margin-bottom: 3rem;
				}
				.table {
				  border-collapse: collapse;
				  border-style: hidden;
				}
				.lastdivider{
					margin-top: 4.5rem;
				}
				.socialmediamotorist a{
					font-size: 1.5rem;
    			padding-top: 0;
				}
			}
			.carousel-control-next span, .carousel-control-prev span{
				color: $halfgray;
				font-size: 3rem;
			}
		}
	}
}
.footer{
	height: 55vh;
	background: $black;
	z-index: 997;
	.container{
		height: 100%;
		.row:first-of-type{
			height: 45vh;
			padding-top: 8vh;
			h5{
				margin-bottom: 1rem;
			}
			i{
				font-size: 3rem;
			}
			p{
				font-size: .9rem;
			}
		}
		.row:not(first-of-type){
			a{
				font-size: .8rem;
			}
		}
	}
}
